import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { get, throttle } from 'lodash';
import { loadTreatment } from '@surfline/web-common/dist/features/redux';
import { ContentContainer, PageRail } from '@surfline/quiver-react';
import PromoBox from '../../components/PromoBox';
import FeedIntro from '../FeedIntro';
import LoadMore from '../LoadMore';
import FeedArticle from '../../components/FeedArticle';
import COTMForecastTrending from './COTMForecastTrending';
import EditorsPicksWithAd from './EditorsPicksWithAd';
import articlePropType from '../../propTypes/article';
import subregionProptype from '../../propTypes/subregion';
import camOfTheMomentProptype from '../../propTypes/camOfTheMoment';
import userDetailsProptype from '../../propTypes/userDetails';
import userSettingsProptype from '../../propTypes/userSettings';
import forecastPropType from '../../propTypes/forecast';
import { trackEvent, detectedAdBlock, clickedSubscribeCTA } from '../../actions/segmentEvents';
import { fetchCamOfTheMoment } from '../../actions/camOfTheMoment';
import {
  fetchForecastChart,
  fetchSubregionForecast,
  setHomeForecast,
} from '../../actions/forecast';
import {
  getArticles,
  getSubregions,
  getCamOfTheMoment,
  getSubregionForecast,
} from '../../selectors/homepage';
import { getPremiumStatus, getUserDetails, getUserSettings } from '../../selectors/user';

class Feed extends Component {
  constructor() {
    super();
    this.state = {
      device: 'MOBILE',
    };
  }

  componentDidMount() {
    const {
      doFetchCamOfTheMoment,
      doFetchSubregionForecast,
      userSettings,
    } = this.props;

    doFetchCamOfTheMoment();

    const subregion = (userSettings && userSettings.homeForecast)
      ? userSettings.homeForecast
      : null;
    doFetchSubregionForecast(subregion);

    this.setDevice();
    window.addEventListener('resize', this.handleResize);
  }

  componentDidUpdate(prevProps) {
    const { forecast, doFetchForecastChart } = this.props;
    const subregionId = get(forecast, 'associated.advertising.subregionId', null);
    if (subregionId && forecast.associated !== prevProps.forecast.associated) {
      doFetchForecastChart(subregionId);
    }
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  setDevice = () => {
    this.setState({
      device: window.innerWidth < 1023 ? 'MOBILE' : 'DESKTOP',
    });
  };

  handleResize = throttle(this.setDevice, 250);

  handleDetectedAdBlock = (spotId, spotName) => {
    this.props.doDetectedAdBlock({
      spotId,
      spotName,
      platform: 'web',
    });
  };

  numActiveFilters = subregions => (
    subregions.length
      ? subregions.filter(subregion => subregion.filtered).length || 'All'
      : null
  );

  render() {
    const { device } = this.state;
    const {
      articles: {
        targeted: targetedArticles,
        promoted: promotedArticles,
      },
      subregions,
      promoBoxSlides,
      isPremium,
      doTrackFeed,
      camOfTheMoment,
      forecast,
      doClickedSubscribeCTA,
      onSetHomeForecast,
      adblock,
      qaFlag,
      userDetails,
    } = this.props;

    const isLoggedIn = !!userDetails;
    const numFilters = this.numActiveFilters(subregions);
    const firstTwoArticles = targetedArticles.slice(0, 2);
    const nextThreeArticles = targetedArticles.slice(2, 5);
    const remainingArticles = targetedArticles.slice(5);

    const renderFeedArticles = articles => articles.map(article => (
      <FeedArticle
        doTrackFeed={doTrackFeed}
        key={article.id}
        article={article}
        isPremium={isPremium}
        numFilters={numFilters}
      />
    ));

    return (
      <div className="sl-feed">
        <ContentContainer>
          <PageRail side="left">
            {
              promoBoxSlides.length ?
                <PromoBox
                  promoBoxSlides={promoBoxSlides}
                  doTrackFeed={doTrackFeed}
                /> : null
            }
            <FeedIntro subregions={subregions} doTrackFeed={doTrackFeed} />
            {renderFeedArticles(firstTwoArticles)}
            <COTMForecastTrending
              cotmId="mobile"
              camOfTheMoment={device === 'MOBILE' ? camOfTheMoment : null}
              isPremium={isPremium}
              adblock={adblock}
              onDetectedAdBlock={this.handleDetectedAdBlock}
              onClickedSubscribeCTA={doClickedSubscribeCTA}
              onSetHomeForecast={onSetHomeForecast}
              qaFlag={qaFlag}
              forecast={forecast}
              promotedArticles={promotedArticles}
              doTrackFeed={doTrackFeed}
              numFilters={numFilters}
              isLoggedIn={isLoggedIn}
            />
            {renderFeedArticles(nextThreeArticles)}
            <EditorsPicksWithAd
              promotedArticles={promotedArticles}
              numFilters={numFilters}
              qaFlag={qaFlag}
              doTrackFeed={doTrackFeed}
            />
            {renderFeedArticles(remainingArticles)}
          </PageRail>
          <PageRail side="right">
            <COTMForecastTrending
              cotmId="desktop"
              camOfTheMoment={device === 'DESKTOP' ? camOfTheMoment : null}
              isPremium={isPremium}
              adblock={adblock}
              onDetectedAdBlock={this.handleDetectedAdBlock}
              onClickedSubscribeCTA={doClickedSubscribeCTA}
              onSetHomeForecast={onSetHomeForecast}
              qaFlag={qaFlag}
              forecast={forecast}
              promotedArticles={promotedArticles}
              doTrackFeed={doTrackFeed}
              numFilters={numFilters}
              isLoggedIn={isLoggedIn}
            />
            <EditorsPicksWithAd
              promotedArticles={promotedArticles}
              numFilters={numFilters}
              isDesktop
              qaFlag={qaFlag}
              doTrackFeed={doTrackFeed}
            />
          </PageRail>
        </ContentContainer>
        <ContentContainer>
          <LoadMore />
        </ContentContainer>
      </div>
    );
  }
}

Feed.propTypes = {
  articles: PropTypes.shape({
    targeted: PropTypes.arrayOf(articlePropType),
    promoted: PropTypes.shape({
      trending: PropTypes.arrayOf(articlePropType),
      editorsPicks: PropTypes.arrayOf(articlePropType),
    }),
  }).isRequired,
  subregions: PropTypes.arrayOf(subregionProptype).isRequired,
  promoBoxSlides: PropTypes.arrayOf(articlePropType).isRequired,
  isPremium: PropTypes.bool,
  doTrackFeed: PropTypes.func.isRequired,
  doFetchCamOfTheMoment: PropTypes.func.isRequired,
  doFetchForecastChart: PropTypes.func.isRequired,
  doFetchSubregionForecast: PropTypes.func.isRequired,
  doDetectedAdBlock: PropTypes.func.isRequired,
  doClickedSubscribeCTA: PropTypes.func.isRequired,
  onSetHomeForecast: PropTypes.func.isRequired,
  camOfTheMoment: camOfTheMomentProptype,
  forecast: forecastPropType.isRequired,
  adblock: PropTypes.bool,
  qaFlag: PropTypes.string,
  userDetails: userDetailsProptype,
  userSettings: userSettingsProptype,
};

Feed.defaultProps = {
  isPremium: false,
  camOfTheMoment: null,
  adblock: false,
  qaFlag: null,
  userDetails: null,
  userSettings: null,
};

export default connect(
  state => ({
    isPremium: getPremiumStatus(state),
    promoBoxSlides: state.homepage.promobox.slides,
    articles: getArticles(state),
    subregions: getSubregions(state),
    camOfTheMoment: getCamOfTheMoment(state),
    forecast: getSubregionForecast(state),
    adblock: state.adblock,
    userDetails: getUserDetails(state),
    userSettings: getUserSettings(state),
  }),
  dispatch => ({
    doLoadTreatment: treatment => dispatch(loadTreatment(treatment)),
    doTrackFeed: (name, properties) => dispatch(trackEvent(name, properties)),
    doDetectedAdBlock: properties => dispatch(detectedAdBlock(properties)),
    doFetchCamOfTheMoment: () => dispatch(fetchCamOfTheMoment()),
    doFetchForecastChart: legacySubregionId => dispatch(fetchForecastChart(legacySubregionId)),
    doFetchSubregionForecast: subregionId => dispatch(fetchSubregionForecast(subregionId)),
    doClickedSubscribeCTA: properties => dispatch(clickedSubscribeCTA(properties)),
    onSetHomeForecast: subregionId => dispatch(setHomeForecast(subregionId)),
  }),
)(Feed);
