import PropTypes from 'prop-types';
import React from 'react';

const TrendingImage = ({ imageUrl, rank, children }) => (
  <div
    className="sl-trending-image"
    style={{ backgroundImage: `url(${imageUrl})` }}
  >
    <div className="sl-ar-content sl-trending-image__rank">{rank}</div>
    {children}
  </div>
);

TrendingImage.propTypes = {
  imageUrl: PropTypes.string.isRequired,
  rank: PropTypes.number.isRequired,
  children: PropTypes.element,
};

TrendingImage.defaultProps = {
  children: null,
};

export default TrendingImage;
