import PropTypes from 'prop-types';
import React from 'react';
import en from '../../intl/translations/en';
import articlePropTypes from '../../propTypes/article';
import numFiltersPropType from '../../propTypes/numFilters';
import TrendingImage from '../TrendingImage';
import TrendingText from '../TrendingText';

const TrendingArticles = ({ articles, doTrackFeed, numFilters }) => {
  if (articles.length === 0) return null;
  const topArticle = articles[0];
  const trendingArticles = articles.slice(1);
  return (
    <div className="sl-trending">
      <div className="sl-trending__content">
        <div className="sl-trending__header">
          {en.Feed.trending.title}
        </div>
        <a
          href={topArticle.permalink}
          target={topArticle.newWindow ? '_blank' : '_self'}
          className="sl-trending__top-item"
          onClick={() => doTrackFeed('Clicked Home Feed Link',
            {
              category: 'Home Page Trending Articles',
              destinationUrl: topArticle.permalink,
              mediaType: (topArticle.media && topArticle.media.type) ? topArticle.media.type : null,
              contentId: topArticle.id,
              numFilters,
            })}
        >
          <TrendingImage imageUrl={topArticle.media.feed2x} rank={1} >
            <TrendingText title={topArticle.content.title} />
          </TrendingImage>
        </a>
        <div className="sl-trending__items">
          {trendingArticles.map((article, index) => (
            <a
              href={article.permalink}
              key={article.id}
              className="sl-trending__item"
              onClick={() => doTrackFeed('Clicked Home Feed Link',
                {
                  category: 'Home Page Trending Articles',
                  destinationUrl: article.permalink,
                  mediaType: (article.media && article.media.type) ? article.media.type : null,
                  contentId: article.id,
                  numFilters,
                })}
            >
              <TrendingImage imageUrl={article.media.feed2x} rank={index + 2} />
              <TrendingText title={article.content.title} />
            </a>
          ))}
        </div>
      </div>
    </div>
  );
};

TrendingArticles.propTypes = {
  articles: PropTypes.arrayOf(articlePropTypes).isRequired,
  doTrackFeed: PropTypes.func.isRequired,
  numFilters: numFiltersPropType,
};

TrendingArticles.defaultProps = {
  numFilters: null,
};

export default TrendingArticles;
