import PropTypes from 'prop-types';
import React from 'react';
import trackForecastOutlookLink from '../../utils/trackForecastOutlookLink';

const ForecastLinks = ({ mapUrl, forecastUrl, doTrackFeed }) => (
  <ul className="sl-forecast-links">
    <li>
      <a
        href={mapUrl}
        onClick={() => trackForecastOutlookLink(doTrackFeed, {
          linkName: 'View Area on Map',
          linkUrl: mapUrl,
        })}
      >
        View Area on Map
      </a>
    </li>
    <li>
      <a
        href={forecastUrl}
        onClick={() => trackForecastOutlookLink(doTrackFeed, {
          linkName: 'View 17-day forecast',
          linkUrl: forecastUrl,
        })}
      >
        View 17-day forecast
      </a>
    </li>
  </ul>
);

ForecastLinks.propTypes = {
  mapUrl: PropTypes.string.isRequired,
  forecastUrl: PropTypes.string.isRequired,
  doTrackFeed: PropTypes.func.isRequired,
};

export default ForecastLinks;
