import PropTypes from 'prop-types';
import React from 'react';
import classNames from 'classnames';
import { CollapsibleModule } from '@surfline/quiver-react';
import subregionPropType from '../../propTypes/subregion';
import trackForecastOutlookLink from '../../utils/trackForecastOutlookLink';
import config from '../../config';

const getSetRegionClassNames = (isLoggedIn, subregions) => classNames({
  'sl-forecast-outlook-header__set-region__message': true,
  'sl-forecast-outlook-header__set-region__message--list': isLoggedIn && !!subregions.length,
});

const handleSetRegion = (sub, onSetHomeForecast, doTrackFeed) => {
  onSetHomeForecast(sub._id);
  doTrackFeed('Clicked Set Region', {
    subregionId: sub._id,
    subregionName: sub.name,
  });
};

const getSetRegionMessage = (isLoggedIn, subregions, doTrackFeed) => {
  if (!isLoggedIn) {
    return (
      <p>
        <a
          href={config.signInUrl}
          onClick={() => trackForecastOutlookLink(doTrackFeed, {
            linkName: 'Sign In',
            linkUrl: config.signInUrl,
          })}
        >
          Sign In
        </a>
        {' to build your favorites list and customize your homepage forecast'}
      </p>
    );
  }
  if (!subregions.length) {
    return (
      <p>
        {'Add '}
        <a
          href={config.favoritesUrl}
          onClick={() => trackForecastOutlookLink(doTrackFeed, {
            linkName: 'Favorites',
            linkUrl: config.favoritesUrl,
          })}
        >
          Favorites
        </a>
        {' to adjust your homepage forecast'}
      </p>
    );
  }
  return (
    <p>
      {'Regions above are displayed based on spots in your '}
      <a
        href={config.favoritesUrl}
        onClick={() => trackForecastOutlookLink(doTrackFeed, {
          linkName: 'Favorites',
          linkUrl: config.favoritesUrl,
        })}
      >
        Favorites
      </a>
      {' list'}
    </p>
  );
};

const ForecastTitle = ({
  doTrackFeed,
  isLoggedIn,
  onSetHomeForecast,
  subregionName,
  subregions,
}) => (
  <div className="sl-forecast-outlook-header">
    <CollapsibleModule title={`${subregionName} Forecast`}>
      <div className="sl-forecast-outlook-header__set-region">
        {(isLoggedIn && subregions.length) ?
          <ul className="sl-forecast-outlook-header__set-region__menu">
            {subregions.map(sub =>
              <li
                className="sl-forecast-outlook-header__set-region__menu__item"
                key={sub._id}
                onClick={() => handleSetRegion(sub, onSetHomeForecast, doTrackFeed)}
              >
                {sub.name}
              </li>,
            )}
          </ul>
          : null}
        <div className={getSetRegionClassNames(isLoggedIn, subregions)}>
          {getSetRegionMessage(isLoggedIn, subregions, doTrackFeed)}
        </div>
      </div>
    </CollapsibleModule>
  </div>
);

ForecastTitle.propTypes = {
  isLoggedIn: PropTypes.bool.isRequired,
  doTrackFeed: PropTypes.func.isRequired,
  onSetHomeForecast: PropTypes.func.isRequired,
  subregionName: PropTypes.string.isRequired,
  subregions: PropTypes.arrayOf(subregionPropType).isRequired,
};

export default ForecastTitle;
