import PropTypes from 'prop-types';
import React from 'react';
import {
  CamPlayer,
  SurfConditions,
  SurfHeight,
  TideArrow,
} from '@surfline/quiver-react';
import en from '../../intl/translations/en';
import config from '../../config';
import camOfTheMomentProptypes from '../../propTypes/camOfTheMoment';
import spotReportPath from '../../utils/urls/spotReportPath';

/** TODO: find a better way to handle errors */
const CamOfTheMoment = ({
  id,
  camOfTheMoment,
  isPremium,
  adblock,
  onDetectedAdBlock,
  onClickedSubscribeCTA,
  qaFlag,
 }) => {
  const { spot, units, advertising } = camOfTheMoment;
  const conditions = spot.conditions.value || 'LOLA';

  const staticUpsellSegmentProperties = {
    ctaLocation: 'COTM_top',
    pageName: 'homepage',
    spotId: spot.spotId,
  };

  const midRollSegmentProperties = {
    ctaLocation: 'mid roll',
    pageName: 'hompage',
    spotId: spot.spotId,
  };

  const adBlockSegmentProperties = {
    ctaLocation: 'cam player ad blocker',
    pageName: 'homepage',
    spotId: spot.spotId,
  };

  const ctaLink = (
    <a
      href={config.funnelUrl}
      onClick={() => onClickedSubscribeCTA(staticUpsellSegmentProperties)}
    >
      Go Premium
    </a>
  );

  return (
    <div className="sl-cam-of-the-moment">
      <div className="sl-cam-of-the-moment__content">
        <div className="sl-cam-of-the-moment__title" >
          {en.Feed.camOfTheMoment.title}
          { !isPremium ?
            <span className="sl-cam-of-the-moment__title__cta" >
              Stream ad-free. {ctaLink}
            </span>
          : null }
        </div>
        <div className="sl-cam-of-the-moment__cam">
          <CamPlayer
            playerId={`sl-cam-of-the-moment__player--${id}`}
            camera={spot.camera}
            advertisingIds={advertising}
            spotName={spot.spotName}
            spotId={spot._id}
            legacySpotId={spot.legacySpotId}
            isPremium={isPremium}
            adblock={adblock && !isPremium}
            funnelUrl={config.funnelUrl}
            rewindUrl={isPremium ?
              config.camRewindUrl(spot.legacySpotId) :
              config.camRewindUrlNonPremium}
            companionHandler={() => true}
            onDetectedAdBlock={() => onDetectedAdBlock(spot._id, spot.spotName)}
            onClickAdBlock={() => onClickedSubscribeCTA(adBlockSegmentProperties)}
            onClickUpSell={() => onClickedSubscribeCTA(midRollSegmentProperties)}
            viewtype={'HOME'}
            adTag={{ iu: '/1024858/Video_Home' }}
            qaFlag={qaFlag}
          />
        </div>
        <div className="sl-cam-of-the-moment__details">
          <div className="sl-cam-of-the-moment__details__spot-name">
            <SurfConditions conditions={conditions} />
            <a href={spotReportPath(spot._id, spot.name)} >
              {spot.name}
            </a>
          </div>
          <div className="sl-cam-of-the-moment__details__conditions">
            <span className="sl-cam-of-the-moment__details__conditions__surf">
              <SurfHeight
                min={spot.waveHeight.min}
                max={spot.waveHeight.max}
                units={units.waveHeight}
                plus={spot.waveHeight.plus}
              />
            </span>
            { spot.tide ?
              <span className="sl-cam-of-the-moment__details__conditions__tide">
                {spot.tide.current.height}{units.tideHeight}
                <TideArrow direction={spot.tide.next.type === 'LOW' ? 'DOWN' : 'UP'} />
              </span> : null
            }
            <span className="sl-cam-of-the-moment__details__conditions__wind">
              {spot.wind.speed}{units.windSpeed}
            </span>
          </div>
        </div>
      </div>
    </div>
  );
};

CamOfTheMoment.propTypes = {
  id: PropTypes.string.isRequired,
  isPremium: PropTypes.bool,
  adblock: PropTypes.bool,
  camOfTheMoment: camOfTheMomentProptypes,
  onDetectedAdBlock: PropTypes.func.isRequired,
  onClickedSubscribeCTA: PropTypes.func.isRequired,
  qaFlag: PropTypes.string,
};

CamOfTheMoment.defaultProps = {
  isPremium: false,
  adblock: false,
  camOfTheMoment: {},
  qaFlag: null,
};

export default CamOfTheMoment;
