import PropTypes from 'prop-types';
import React from 'react';
import CamOfTheMoment from '../../components/CamOfTheMoment';
import COTMPlaceholder from '../../components/CamOfTheMoment/COTMPlaceholder';
import ForecastOutlook from '../../components/ForecastOutlook';
import TrendingArticles from '../../components/TrendingArticles';
import articlePropTypes from '../../propTypes/article';
import camOfTheMomentProptype from '../../propTypes/camOfTheMoment';
import forecastPropType from '../../propTypes/forecast';
import numFiltersPropType from '../../propTypes/numFilters';

const COTMForecastTrending = ({
  camOfTheMoment,
  isPremium,
  adblock,
  onDetectedAdBlock,
  onClickedSubscribeCTA,
  onSetHomeForecast,
  qaFlag,
  forecast,
  promotedArticles,
  doTrackFeed,
  numFilters,
  cotmId,
  isLoggedIn,
}) => (
  <div className="sl-feed__cotm-forecast-trending">
    {camOfTheMoment ? (
      <CamOfTheMoment
        id={cotmId}
        isPremium={isPremium}
        adblock={adblock}
        camOfTheMoment={camOfTheMoment}
        onDetectedAdBlock={onDetectedAdBlock}
        onClickedSubscribeCTA={onClickedSubscribeCTA}
        qaFlag={qaFlag}
      />
    ) : <COTMPlaceholder />}
    {forecast.success ? (
      <ForecastOutlook
        subregionId={forecast.overview._id}
        subregionName={forecast.overview.name}
        summary={forecast.overview.forecastSummary}
        breadcrumb={forecast.overview.breadcrumb}
        conditions={forecast.conditions}
        utcOffset={forecast.associated.utcOffset}
        units={forecast.associated.units}
        forecastChart={forecast.chart}
        onSetHomeForecast={onSetHomeForecast}
        subregions={forecast.subregions}
        isLoggedIn={isLoggedIn}
        doTrackFeed={doTrackFeed}
        loading={forecast.loading}
      />
    ) : null}
    <TrendingArticles
      articles={promotedArticles.trending}
      doTrackFeed={doTrackFeed}
      numFilters={numFilters}
    />
  </div>
);

COTMForecastTrending.propTypes = {
  camOfTheMoment: camOfTheMomentProptype,
  isPremium: PropTypes.bool,
  adblock: PropTypes.bool,
  onDetectedAdBlock: PropTypes.func.isRequired,
  onClickedSubscribeCTA: PropTypes.func.isRequired,
  onSetHomeForecast: PropTypes.func.isRequired,
  qaFlag: PropTypes.string,
  forecast: forecastPropType.isRequired,
  promotedArticles: PropTypes.shape({
    trending: PropTypes.arrayOf(articlePropTypes),
    editorsPick: PropTypes.arrayOf(articlePropTypes),
  }).isRequired,
  doTrackFeed: PropTypes.func.isRequired,
  numFilters: numFiltersPropType,
  cotmId: PropTypes.string.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
};

COTMForecastTrending.defaultProps = {
  promotedArticles: {
    trending: [],
    editorsPick: [],
  },
  isPremium: false,
  camOfTheMoment: null,
  numFilters: null,
  adblock: false,
  qaFlag: null,
};

export default COTMForecastTrending;
