import PropTypes from 'prop-types';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import classNames from 'classnames';
import { fetchArticles } from '../../actions/homepage';
import {
  updateFilteredSubregions,
  // initializeFilteredSubregions
} from '../../actions/subregions';
import { updateUserSettings } from '../../actions/user';
import { getSubregions } from '../../selectors/homepage';
import { getUserSettings } from '../../selectors/user';
import en from '../../intl/translations/en';
import config from '../../config';
import subregionProptype from '../../propTypes/subregion';

class FeedIntro extends Component {
  // componentDidMount() {
  //   const { initialFilteredSubregions, doInitializeFilteredSubregions } = this.props;
  //   doInitializeFilteredSubregions(initialFilteredSubregions);
  // }

  componentDidUpdate() {
    const { subregions, doUpdateUserSettings, initialFilteredSubregions } = this.props;
    const subregionFilters = subregions.filter(({ filtered }) => filtered).map(({ _id }) => _id).join(',');
    if (subregionFilters !== initialFilteredSubregions) {
      doUpdateUserSettings({ feedFilters: { subregions: subregionFilters } });
    }
  }

  subregionFilterClassName = isSelected => classNames({
    'sl-feed-intro__subregion': true,
    'sl-feed-intro__subregion--on': isSelected,
  });

  handleClickedSubregion = (subregion) => {
    const { doUpdateFilteredSubregions, doFetchArticles, doTrackFeed } = this.props;
    doTrackFeed('Clicked Feed Filter', {
      subregionId: subregion ? subregion._id : 'All',
      subregionName: subregion ? subregion.name : 'All',
      filterState: subregion && subregion.filtered ? 'off' : 'on',
    });
    doUpdateFilteredSubregions(subregion ? subregion._id : null);
    doFetchArticles(true);
  }

  render() {
    const { subregions } = this.props;
    const selectAll = subregions.every(({ filtered }) => !filtered);
    const withSubregions = subregions.length > 0;
    const message = en.Feed.feedIntro[withSubregions ? 'messageWithSubregions' : 'messageWithoutSubregions'];
    return (
      <div className="sl-feed-intro">
        <div>
          <div className="sl-feed-intro__title">{en.Feed.feedIntro.title}</div>
          <div
            className="sl-feed-intro__message"
            dangerouslySetInnerHTML={{ __html: message(config.favoritesUrl) }}
          />
          {withSubregions ? (
            <div className="sl-feed-intro__subregions">
              <button
                key="all"
                className={this.subregionFilterClassName(selectAll)}
                onClick={() => this.handleClickedSubregion(null)}
              >
                All
              </button>
              {subregions.map(subregion => (
                <button
                  key={subregion._id}
                  className={this.subregionFilterClassName(subregion.filtered)}
                  onClick={() => this.handleClickedSubregion(subregion)}
                >
                  {subregion.name}
                </button>
              ))}
            </div>
          ) : null}
        </div>
      </div>
    );
  }
}

FeedIntro.propTypes = {
  subregions: PropTypes.arrayOf(subregionProptype).isRequired,
  initialFilteredSubregions: PropTypes.string.isRequired,
  doUpdateFilteredSubregions: PropTypes.func.isRequired,
  // doInitializeFilteredSubregions: PropTypes.func.isRequired,
  doUpdateUserSettings: PropTypes.func.isRequired,
  doFetchArticles: PropTypes.func.isRequired,
  doTrackFeed: PropTypes.func.isRequired,
};

export default connect(
  state => ({
    subregions: getSubregions(state),
    initialFilteredSubregions: getUserSettings(state).feedFilters.subregions,
  }),
  dispatch => ({
    doUpdateFilteredSubregions: subregionId => dispatch(updateFilteredSubregions(subregionId)),
    doUpdateUserSettings: userSettings => dispatch(updateUserSettings(userSettings)),
    // doInitializeFilteredSubregions: initialFilteredSubregions =>
    //   dispatch(initializeFilteredSubregions(initialFilteredSubregions)),
    doFetchArticles: () => dispatch(fetchArticles(true)),
  }),
)(FeedIntro);
