import PropTypes from 'prop-types';
import React from 'react';
import { Spinner } from '@surfline/quiver-react';

const ForecastChart = ({ loading, subregionName, url }) => (
  <div>
    {loading ?
      <div className="sl-forecast-chart--loading"><Spinner /></div>
      : null}
    {url ?
      <img
        src={url}
        alt={`${subregionName} Forecast`}
        className="sl-forecast-chart"
      />
      : null}
  </div>
);

ForecastChart.propTypes = {
  loading: PropTypes.bool.isRequired,
  subregionName: PropTypes.string,
  url: PropTypes.string,
};

ForecastChart.defaultProps = {
  url: null,
  subregionName: null,
};

export default ForecastChart;
