import PropTypes from 'prop-types';
import React from 'react';
import { distanceInWordsStrict } from 'date-fns';
import { EditorialArticleCard, ForecastArticleCard } from '@surfline/quiver-react';
import articlePropTypes from '../../propTypes/article';
import numFiltersPropType from '../../propTypes/numFilters';
import config from '../../config';

const FeedArticle = ({ article, isPremium, doTrackFeed, numFilters }) => (
  <div className="sl-feed__article">
    <div className="sl-feed__article__content">
      {article.contentType === 'EDITORIAL' ?
        <EditorialArticleCard
          author={article.author.name}
          externalLink={article.externalLink}
          externalSource={article.externalSource}
          newWindow={article.newWindow}
          publishedDateTime={distanceInWordsStrict((article.updatedAt * 1000), new Date())}
          premium={article.premium}
          title={article.content.title}
          subtitle={article.content.subtitle}
          permalink={article.permalink}
          media={article.media}
          tags={article.tags}
          showPaywall={false} // Editoral articles should never show paywalls.
          funnelUrl={config.funnelUrl}
          onClickContent={() => doTrackFeed('Clicked Home Feed Link',
            {
              category: 'Home Page Feed',
              destinationUrl: article.permalink,
              mediaType: (article.media && article.media.type) ? article.media.type : null,
              contentId: article.id,
              numFilters,
            })}
          onClickPaywall={() => doTrackFeed('Clicked Subscribe CTA', {
            ctaLocation: 'Home Page Feed',
            contentId: article.id,
          })}
          onClickTag={tag => doTrackFeed('Clicked Home Feed Link',
            {
              category: 'Home Page Feed',
              destinationUrl: tag.url,
              mediaType: tag.name,
              contentId: article.id,
              numFilters,
            },
          )}
        /> :
        <ForecastArticleCard
          publishedDateTime={distanceInWordsStrict((article.updatedAt * 1000), new Date())}
          premium={article.premium}
          expert={article.promoted ? article.promoted.includes('REGIONAL') : false}
          title={article.content.title}
          body={article.content.body}
          author={article.author ? article.author.name : null}
          id={article.id}
          tags={article.tags}
          showPaywall={!isPremium && article.premium}
          funnelUrl={config.funnelUrl}
          onClickPaywall={() => doTrackFeed('Clicked Subscribe CTA', {
            ctaLocation: 'Home Page Feed',
            contentId: article.id,
          })}
          onClickReadMore={() => doTrackFeed('Clicked Read More', {
            location: 'home page feed',
            contentId: article.id,
            title: article.content.title,
            premiumArticle: article.premium,
            numFilters,
          })}
          onClickShareArticle={shareChannel => doTrackFeed('Clicked Share Icon', {
            title: article.content.title,
            contentId: article.id,
            tags: article.tags.map(tag => tag.name).join(','),
            locationCategory: 'Surf Feed',
            destinationUrl: article.permalink,
            mediaType: article.media ? article.media.type : null,
            shareChannel,
          })}
          doTrackFeed={doTrackFeed}
          segmentCategory={'Home Page Feed'}
          fbAppId={config.appKeys.fbAppId}
          url={article.permalink}
          showShare={isPremium}
        />
      }
    </div>
  </div>
);

FeedArticle.propTypes = {
  article: articlePropTypes.isRequired,
  isPremium: PropTypes.bool.isRequired,
  doTrackFeed: PropTypes.func.isRequired,
  numFilters: numFiltersPropType,
};

FeedArticle.defaultProps = {
  numFilters: null,
};

export default FeedArticle;
