import PropTypes from 'prop-types';
import React from 'react';
import en from '../../intl/translations/en';
import articlePropTypes from '../../propTypes/article';
import numFiltersPropType from '../../propTypes/numFilters';

const EditorsPicks = ({ articles, doTrackFeed, numFilters }) => (
  <div className="sl-editors-picks">
    <div className="sl-editors-picks__content">
      <div className="sl-editors-picks__header">
        {en.Feed.editorsPicks.title}
      </div>
      <div className="sl-editors-picks__items">
        {articles.map(article => (
          <a
            href={article.permalink}
            target={article.newWindow ? '_blank' : '_self'}
            key={article.id}
            className="sl-editors-pick"
            onClick={() => doTrackFeed('Clicked Home Feed Link',
              {
                category: 'Home Page Editors Pick',
                destinationUrl: article.permalink,
                mediaType: (article.media && article.media.type) ? article.media.type : null,
                contentId: article.id,
                numFilters,
              })}
          >
            <div
              className="sl-editors-pick__image"
              style={{ backgroundImage: `url(${article.media.feed2x})` }}
            />
            <div className="sl-editors-pick__title">
              <span>{article.content.title}</span>
            </div>
          </a>
        ))}
      </div>
    </div>
  </div>
);

EditorsPicks.propTypes = {
  articles: PropTypes.arrayOf(articlePropTypes).isRequired,
  doTrackFeed: PropTypes.func.isRequired,
  numFilters: numFiltersPropType,
};

EditorsPicks.defaultProps = {
  numFilters: null,
};

export default EditorsPicks;
