import PropTypes from 'prop-types';
import React from 'react';
import { ForecasterProfile, Spinner } from '@surfline/quiver-react';
import subregionForecastPath from '../../utils/urls/subregionForecastPath';
import subregionPropType from '../../propTypes/subregion';
import unitsPropType from '../../propTypes/units';
import forecastConditionPropType from '../../propTypes/forecastCondition';
import forecastSummaryPropType from '../../propTypes/forecastSummary';
import ForecastChart from '../ForecastChart';
import ForecastConditions from '../ForecastConditions';
import ForecastOutlookHeader from '../ForecastOutlookHeader';
import ForecastLinks from '../ForecastLinks';

const ForecastOutlook = ({
  subregionId,
  subregionName,
  summary,
  breadcrumb,
  conditions,
  units,
  utcOffset,
  forecastChart,
  subregions,
  onSetHomeForecast,
  doTrackFeed,
  isLoggedIn,
  loading,
}) => (
  <div className="sl-forecast-outlook">
    {!loading ?
      <div>
        <ForecastOutlookHeader
          onSetHomeForecast={onSetHomeForecast}
          subregionName={subregionName}
          subregions={subregions}
          isLoggedIn={isLoggedIn}
          doTrackFeed={doTrackFeed}
        />
        <div className="sl-forecast-outlook__observation">
          <ForecastChart loading={forecastChart.loading} url={forecastChart.url} />
          {summary.forecaster ?
            <ForecasterProfile
              forecasterName={summary.forecaster.name}
              forecasterTitle={summary.forecaster.title}
              url={summary.forecaster.iconUrl}
            />
          : null}
          {summary.highlights.length ?
            <ul className="sl-forecast-outlook__observation__highlights">
              {summary.highlights.map(highlight => <li key={highlight}>{highlight}</li>)}
            </ul>
            : null}
        </div>
        <div className="sl-forecast-outlook__conditions">
          {conditions.slice(0, 2).map(condition => (
            <ForecastConditions
              key={condition.timestamp}
              timestamp={condition.timestamp}
              am={condition.am}
              pm={condition.pm}
              units={units}
              utcOffset={utcOffset}
            />
          ))}
        </div>
        <ForecastLinks
          mapUrl={breadcrumb[breadcrumb.length - 1].href}
          forecastUrl={subregionForecastPath(subregionId, subregionName)}
          doTrackFeed={doTrackFeed}
        />
      </div>
      : <div className="sl-forecast-outlook--loading"><Spinner /></div>
    }
  </div>
);

ForecastOutlook.propTypes = {
  subregionId: PropTypes.string.isRequired,
  subregionName: PropTypes.string.isRequired,
  summary: forecastSummaryPropType.isRequired,
  breadcrumb: PropTypes.arrayOf(PropTypes.shape({
    href: PropTypes.string,
    name: PropTypes.string,
  })).isRequired,
  conditions: PropTypes.arrayOf(forecastConditionPropType).isRequired,
  units: unitsPropType.isRequired,
  utcOffset: PropTypes.number.isRequired,
  forecastChart: PropTypes.shape({
    loading: PropTypes.bool,
    url: PropTypes.string,
  }),
  onSetHomeForecast: PropTypes.func.isRequired,
  subregions: PropTypes.arrayOf(subregionPropType).isRequired,
  doTrackFeed: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  loading: PropTypes.bool,
};

ForecastOutlook.defaultProps = {
  forecastChart: PropTypes.shape({
    loading: false,
    url: null,
  }),
  loading: false,
};

export default ForecastOutlook;
