import { connect } from 'react-redux';
import { loadTreatment } from '@surfline/web-common/dist/features/redux';
import CamOfTheMoment from './CamOfTheMoment';

export default connect(
  null,
  dispatch => ({
    doLoadTreatments: treatment => dispatch(loadTreatment(treatment)),
  }),
)(CamOfTheMoment);
