import PropTypes from 'prop-types';
import React from 'react';
import EditorsPicks from '../../components/EditorsPicks';
import FeedAdUnit from '../../components/FeedAdUnit';
import articlePropTypes from '../../propTypes/article';
import numFiltersPropType from '../../propTypes/numFilters';

const EditorsPicksWithAd = ({ promotedArticles, numFilters, isDesktop, qaFlag, doTrackFeed }) => (
  <div className="sl-feed__editors-picks-with-ads">
    <EditorsPicks
      doTrackFeed={doTrackFeed}
      articles={promotedArticles.editorsPick}
      numFilters={numFilters}
    />
    <FeedAdUnit isDesktop={isDesktop} qaFlag={qaFlag} />
  </div>
);

EditorsPicksWithAd.propTypes = {
  promotedArticles: PropTypes.shape({
    trending: PropTypes.arrayOf(articlePropTypes),
    editorsPick: PropTypes.arrayOf(articlePropTypes),
  }).isRequired,
  numFilters: numFiltersPropType,
  isDesktop: PropTypes.bool,
  qaFlag: PropTypes.string,
  doTrackFeed: PropTypes.func.isRequired,
};

EditorsPicksWithAd.defaultProps = {
  numFilters: null,
  isDesktop: false,
  qaFlag: null,
};

export default EditorsPicksWithAd;
